#office {
  position: relative;
  width: 100%;
  height: 100%;
}

#computer-indicator {
  position: absolute;
  color: white;
  background: black;
  animation: bob 1s infinite ease-in-out;
}
@keyframes bob {
  0% {
    transform: translate(-50%, -30px);
  }
  50% {
    transform: translate(-50%, -40px);
  }
  100% {
    transform: translate(-50%, -30px);
  }
}

#computer-screen {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 50%;
  height: 50%;

  border: 1px solid black;
  background: white;
  color: black;
}
